import * as THREE from 'three';

// Scene setup
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
const renderer = new THREE.WebGLRenderer({ antialias: true });
renderer.setSize(window.innerWidth, window.innerHeight);
document.body.appendChild(renderer.domElement);

// Particle and line creation
const particleCount = 200;
const particlePositions = [];
const particles = new THREE.BufferGeometry();
const particleVelocities = new Float32Array(particleCount * 3);

for (let i = 0; i < particleCount; i++) {
    const x = Math.random() * 200 - 100;
    const y = Math.random() * 200 - 100;
    const z = Math.random() * 200 - 100;
    particlePositions.push(x, y, z);
    particleVelocities[i * 3] = (Math.random() - 0.5) * 0.2;
    particleVelocities[i * 3 + 1] = (Math.random() - 0.5) * 0.2;
    particleVelocities[i * 3 + 2] = (Math.random() - 0.5) * 0.2;
}

particles.setAttribute('position', new THREE.Float32BufferAttribute(particlePositions, 3));

const particleMaterial = new THREE.PointsMaterial({
    color: 0xffffff,
    size: 5, // Increased size for better visibility
    sizeAttenuation: true,
    map: new THREE.TextureLoader().load('https://threejsfundamentals.org/threejs/resources/images/disc.png'),
    alphaTest: 0.5,
    transparent: true
});

const particleSystem = new THREE.Points(particles, particleMaterial);
scene.add(particleSystem);

// Line creation
const maxConnections = 4;
const minDistance = 30;
const fadeDistance = minDistance * 1.5;
const lineMaterial = new THREE.LineBasicMaterial({ vertexColors: true, transparent: true });
const linesGeometry = new THREE.BufferGeometry();
const linePositions = [];
const lineColors = [];
const lineAlphas = [];

function updateLines() {
    linePositions.length = 0;
    lineColors.length = 0;
    lineAlphas.length = 0;

    const positions = particles.attributes.position.array;

    for (let i = 0; i < particleCount; i++) {
        let connections = 0;
        for (let j = i + 1; j < particleCount; j++) {
            const dx = positions[i * 3] - positions[j * 3];
            const dy = positions[i * 3 + 1] - positions[j * 3 + 1];
            const dz = positions[i * 3 + 2] - positions[j * 3 + 2];
            const distance = Math.sqrt(dx * dx + dy * dy + dz * dz);

            if (distance < fadeDistance && connections < maxConnections) {
                linePositions.push(
                    positions[i * 3], positions[i * 3 + 1], positions[i * 3 + 2],
                    positions[j * 3], positions[j * 3 + 1], positions[j * 3 + 2]
                );

                const alpha = 1.0 - (distance / fadeDistance);
                lineColors.push(0, 1, 1, alpha);
                lineColors.push(0, 1, 1, alpha);

                connections++;
            }
        }
    }

    linesGeometry.setAttribute('position', new THREE.Float32BufferAttribute(linePositions, 3));
    linesGeometry.setAttribute('color', new THREE.Float32BufferAttribute(lineColors, 4));
    linesGeometry.setDrawRange(0, linePositions.length / 3);
    linesGeometry.attributes.position.needsUpdate = true;
    linesGeometry.attributes.color.needsUpdate = true;
}

const lineSystem = new THREE.LineSegments(linesGeometry, lineMaterial);
scene.add(lineSystem);

camera.position.z = 100;

// Animation loop
function animate() {
    requestAnimationFrame(animate);

    // Update particle positions
    const positions = particles.attributes.position.array;
    for (let i = 0; i < particleCount; i++) {
        positions[i * 3] += particleVelocities[i * 3];
        positions[i * 3 + 1] += particleVelocities[i * 3 + 1];
        positions[i * 3 + 2] += particleVelocities[i * 3 + 2];

        // Wrap particles around the scene bounds
        if (positions[i * 3] > 100 || positions[i * 3] < -100) particleVelocities[i * 3] *= -1;
        if (positions[i * 3 + 1] > 100 || positions[i * 3 + 1] < -100) particleVelocities[i * 3 + 1] *= -1;
        if (positions[i * 3 + 2] > 100 || positions[i * 3 + 2] < -100) particleVelocities[i * 3 + 2] *= -1;
    }
    particles.attributes.position.needsUpdate = true;

    // Update line positions
    updateLines();

    // Render the scene
    renderer.render(scene, camera);
}

animate();

// Handle window resize
window.addEventListener('resize', () => {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
});
